.namebox{
	display: flex;
	align-items: center;
	column-gap: 16px;
}

.namebox h2 {
	font-size: 22px;
	line-height: 1.45; 
	font-weight: 400;
}

.namebox a {
	font-size: 16px;
	line-height: 1.37; 
	font-weight: 500;
	color: var(--color-primary-grey);
}

.list {
	margin-top: 16px;
	list-style-type: none;
	@media screen and (min-width: 834px) {
		width: 100%;
		max-width: 518px;
	}
}

.item {
	@media screen and (min-width: 834px){
		display: flex;
		column-gap: 10px;
	}
}
.item ~ .item {
	padding-top: 28px;
}

.title_container {
	display: flex;
	align-items: center;
	column-gap: 12px;
	width: 148px;
}
.title_container img {
	width: 43px;
	height: 43px;
}

.meal_info{
	margin-top: 8px;
	font-size: 14px;
	line-height: 1.42;
	color: var(--color-primary-white);
	cursor: pointer;
	@media screen and (min-width: 834px){
		display: flex;
	}
}


.meal_info > p ~ p {
	margin-top: 20px;
	@media screen and (min-width: 834px){
		margin-top: 0;
		margin-left: 20px;
	}
}

.meal_info span {
	font-size: 14px;
	line-height: 1.42; 
	font-weight: 500;
	color: var(--color-primary-grey);
}
.button{
	display: flex;
	align-items: center;
	column-gap: 8px;
	border: none;
	background-color: transparent;
	line-height: 1.43;
	font-weight: 500;
	color: var(--color-primary-green-lite);
	cursor: pointer;
}
.button svg {
	fill: var(--color-primary-green-lite);
}

.list .hidden {
	display: none;
}